<template>
<div class="mainXiaoGuo">
   <section class="head">
      <div> <a @click="goBack" class="iconfont back"> &#xe614; </a> </div>
      <h1>All Categories</h1>
       <div class="chartWrap">
            <a class="iconfont cart"> &#xe625; </a>
            <a class="iconfont cart"> &#xe669; </a> </div>
    </section>
    <ul class="twoList">
        <li v-for='(item,index) in categoryOne' :key="index" @click="$router.push({path:'/categoryElectronics',query:{'categoryCode':item.categoryCode,'name':item.categoryName}})">
            <div class="iconWrap">
                <img class="cart" :src="item.img" />
            </div>
            <div class="title">
                {{item.categoryName}}
                <div>
                    <van-icon color="#999999" size="20px" name="arrow" />
                </div>
            </div>
        </li>

    </ul>

</div>
</template>

<script>
import {mapGetters} from 'vuex';

export default {
  name: 'allCategories',
  data () {
    return {


    }
  },
  computed: {
      ...mapGetters(['categoryOne'])
  },
  mounted() {
    this.init();
  },
  methods: {
    init(){

    },
    changeSort(val){
      this.sortValue=val;
      this.sortVisible=false;
    },
    goBack(){
      this.bus.$emit('goBack');
    }


  }
}
</script>

<style lang="scss" scoped>
    .twoList{
        border-top: 10px solid #f6f6f6;
        width: 100%;
        padding-right: 15px;
        li{
            height: 53px;
            display: -webkit-box;
         }
    }

    .iconWrap{
        width: 15%;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: center;
    }
    .iconWrap img{
        width: 100%;
    }
    .title{
        border-bottom: 1px solid #f2f3f5;
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;
        width: 85%;
        font-size: 15px;
        div{
            height: 100%;
            display: flex; align-items: center;
        }
    }
    .red{
        color: red;
    }
</style>



